import React, { useState, useRef } from 'react';


// slide component
const Slider = () => {
  
  const [currentSlide, setCurrentSlide] = useState(true);
  const sliderRef = useRef(null);  
  
  const handleClick = () => {
    if(currentSlide){
      const scrollMove = sliderRef.current.clientWidth;
      sliderRef.current.scrollLeft += scrollMove;
      setCurrentSlide(false);
      return
      }
      const scrollMove = sliderRef.current.clientWidth;
      sliderRef.current.scrollLeft -= scrollMove;
      setCurrentSlide(true);
    }
    
    return (
    <div className="slide-container" ref={sliderRef}>
      <div className="slide-track" >
          <div
          className="slide" 
            onClick={() => handleClick()} //    
          >
              <div className='mainPrevNextMatch'>
                    {/*   */}
                    <div>
                        <span> 
                            <img src="/images/matchCenter/logo_match_kleague.png" alt="" />
                        </span>
                        <p>K-LEAGUE</p>
                        <small></small>
                        <b>10</b>
                    </div>
                    <div>
                        <span>
                            <span>
                                <img src="/images/matchCenter/schedul_icon_.png" alt={`  `} />
                            </span>
                            <strong></strong>
                        </span>
                        3 : 0
                        <span>
                            <span>
                                <img src={`/images/matchCenter/schedul_icon_.png`} alt={`  `} />
                            </span>
                            <strong></strong>
                        </span>
                    </div>
               </div>
          </div>
          <div
          className="slide" 
            onClick={() => handleClick()} //    
          >
              <div className='mainPrevNextMatch'>
                    {/*   */}
                    <div>
                        <span> 
                            <img src="/images/matchCenter/logo_match_kleague.png" alt="" />
                        </span>
                        <p>K-LEAGUE</p>
                        <small></small>
                        <b>11</b>
                    </div>
                    <div>
                        <span>
                            <span>
                                <img src="/images/matchCenter/schedul_icon_.png" alt={`  `} />
                            </span>
                            <strong></strong>
                        </span>
                        4 : 0
                        <span>
                            <span>
                                <img src={`/images/matchCenter/schedul_icon_.png`} alt={`  `} />
                            </span>
                            <strong></strong>
                        </span>
                    </div>
               </div>
          </div>
          <div
          className="slide" 
            onClick={() => handleClick()} //    
          >
              <div className='mainPrevNextMatch'>
                    {/*   */}
                    <div>
                        <span> 
                            <img src="/images/matchCenter/logo_match_kleague.png" alt="" />
                        </span>
                        <p>K-LEAGUE</p>
                        <small></small>
                        <b>12</b>
                    </div>
                    <div>
                        <span>
                            <span>
                                <img src="/images/matchCenter/schedul_icon_.png" alt={`  `} />
                            </span>
                            <strong></strong>
                        </span>
                        1 : 0
                        <span>
                            <span>
                                <img src={`/images/matchCenter/schedul_icon_.png`} alt={`  `} />
                            </span>
                            <strong></strong>
                        </span>
                    </div>
               </div>
          </div>
      </div>
    </div>
  );
};





