import { useState,useEffect } from "react"
import Banner from "../../components/Banner"
import data from "../../data.json"
import React from "react";

const staffs = data.staffs;


export default function CoachingStaff () {

    const [isCoachShow,setIsCoachShow] = useState(true);
    const [isPopup,setIsPopup] = useState (false);
    const [isActiveInPopup,setIsActiveInPopup] = useState (true);
    
    useEffect(() => {
        if(isPopup){
            document.body.style.overflow = 'hidden';
        }else{
            document.body.style.overflow ='auto';
        }
        return () => {
            document.body.style.overflow = 'auto';
        };
        
    },[isPopup]);


    return (
        <>
        <Banner onBannerTab={'/'} aniWidth={'10%'} />
        <section className="coachingStaffArea size1442">
            <h2>/ </h2>
            <div>
                <img src="/images/player/m_.png" alt=" " />
                <div>
                    <button onClick={()=>setIsCoachShow(true)} className={isCoachShow ? 'active' : ''}></button>
                    <button onClick={()=>setIsCoachShow(false)} className={!isCoachShow ? 'active' : ''}></button>
                    {isCoachShow ? 
                        <div className="coachProfile">
                            <span>HEAD COACH</span>
                            <strong> <span>CHOI YOUNG KEUN</span></strong>
                            <ul>
                                <li>
                                <span></span>
                                <p></p>
                                </li>
                                <li>
                                <span></span>
                                <p>1972 7 16</p>
                                </li>
                                <li>
                                <span></span>
                                <p></p>
                                </li>
                            </ul>
                        </div>
                        :
                        <div className="coachBriefHistory">
                            <strong>  <br /> .</strong>
                            <ul>
                                <li>
                                    <span></span>
                                    <div>
                                        <p><span>2012~2015</span> </p>
                                        <p><span>2016~2017</span>  </p>
                                        <p><span>2019</span> </p>
                                        <p><span>2020.01~2020.08</span>  </p>
                                        <p><span>2020.08~2022</span> </p>
                                        <p><span>2023</span>  </p>
                                        <p><span>2024.08~</span> </p> 
                                    </div>
                                </li>
                                <li>
                                    <span></span>
                                    <div>
                                        <p><span>1995~1998</span></p>
                                        <p><span>1998~1999</span></p>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    }
                </div>
                </div>
                <h3>INCHEON UNITED COACH</h3>
                <ul>
                {staffs.map((el, index) => (
                      <React.Fragment key={el.id}>
                        <li key={el.id}>
                          <img src={el.image} alt={el.name} />
                          <button onClick={() => setIsPopup(el.englishName)}></button>
                          <p>
                            {el.name} <br />
                            <span>{el.englishName}</span>
                          </p>
                        </li>
                        <div
                          key={el.id + index} // id   key 
                          className={el.englishName === isPopup ? "popup-OverLay active" : "popup-OverLay"}
                        >
                          <div className="popup-Content">
                            <button onClick={() => setIsPopup('')}> </button>
                            <img src={el.image} alt={el.name} />
                            <div>
                              <div>
                                <button
                                  onClick={() => setIsActiveInPopup(true)}
                                  className={isActiveInPopup ? 'active' : ''}
                                >
                                  
                                </button>
                                <button
                                  onClick={() => setIsActiveInPopup(false)}
                                  className={!isActiveInPopup ? 'active' : ''}
                                >
                                  
                                </button>
                              </div>
                              {isActiveInPopup ? (
                                <>
                                  <p>{el.name} <span>{el.englishName}</span></p>
                                  <ul>
                                    <li><span></span>{el.profile["position"]}</li>
                                    <li><span></span>{el.profile["birthData"]}</li>
                                    <li><span></span>{el.profile["schoolOfOrigin"]}</li>
                                  </ul>
                                </>
                              ) : (
                                <>
                                  <ul>
                                    <li style={{ marginBottom: '20px' }}>
                                      <span></span>
                                      <div>
                                        <p>
                                          {el.briefHistory["LeadershipExperience"].year.map((i, index) => (
                                            <React.Fragment key={index}>{i}<br /></React.Fragment>
                                          ))}
                                        </p>
                                        <p>
                                          {el.briefHistory["LeadershipExperience"].text.map((i, index) => (
                                            <React.Fragment key={index}>{i}<br /></React.Fragment>
                                          ))}
                                        </p>
                                      </div>    
                                    </li>
                                    {el.briefHistory["playerCareer"].year[0] == null ? (
                                      <li>
                                        <span style={{ display: `${el.briefHistory["playerCareer"].year[0] == null && el.briefHistory["playerCareer"].text[0] == null ? 'none' : ''}` }}></span>
                                        <div>
                                          <p>
                                            {el.briefHistory["playerCareer"].text.map((i, index) => (
                                              <React.Fragment key={index}>{i}<br /></React.Fragment>
                                            ))}
                                          </p>
                                        </div>    
                                      </li>
                                    ) : (
                                      <li>
                                        <span></span>
                                        <div>
                                          <p>
                                            {el.briefHistory["playerCareer"].year.map((i, index) => (
                                              <React.Fragment key={index}>{i}<br /></React.Fragment>
                                            ))}
                                          </p>
                                          <p>
                                            {el.briefHistory["playerCareer"].text.map((i, index) => (
                                              <React.Fragment key={index}>{i}<br /></React.Fragment>
                                            ))}
                                          </p>
                                        </div>    
                                      </li>
                                    )}
                                  </ul>
                                </>
                              )}
                            </div>
                          </div>
                        </div>
                      </React.Fragment>
                    ))}
                </ul>
        </section>
        </>
    )
}



