import { useState } from "react"
import { format } from 'date-fns';
import Banner from "../../components/Banner"
import data from "../../data.json"
import SelectedBox from "../../components/matchCenter/SelectedBox";


export default function Rangking () {
    const date = new Date();
    const currentYear = format(date,'yyyy');
    const YEARS =[currentYear,currentYear-1,currentYear-2,currentYear-3,currentYear-4];
    const TODAY = format(date,'yyyyMMdd');
    const rankTableData = data["k-leagueRankTable"];
    const [selectedGameType , setSelectedGameType] = useState('true');
    const [selectedYear , setSelectedYear] = useState(currentYear);

    const dropProps ={
        YEARS,
        selectedYear,
        setSelectedYear,
        selectedGameType,
        setSelectedGameType,
        TODAY
    }

    return (
        <>
        <Banner aniWidth={'100%'} />
        <section className="rankingArea size1442">
        <h2 className="hiddenH2"></h2>
            <SelectedBox props={dropProps} />
            <p> K1 2024</p>
            <table>
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {rankTableData.map((el) => {
                        return (<tr key={el.id}>
                            <td>{el[""]}</td>
                            <td><img src={`/images/matchCenter/${el[""]}.png`} alt={`${el[""]} `} /><span>{el[""]}</span></td>
                            <td>{el[""]}</td>
                            <td>{el[""]}</td>
                            <td>{el[""]}</td>
                            <td>{el[""]}</td>
                            <td>{el[""]}</td>
                            <td>{el[""]}</td>
                            <td>{el[""]}</td>
                            <td>{el[""]}</td>
                            </tr>)
                    })}
                </tbody>
            </table>
        </section>
        </>
    )
}