import {Link, NavLink, useNavigate} from "react-router-dom"
import { useSelector , useDispatch } from "react-redux"
import { ActionLoginLogout } from "../../controllers/Redux/setting.jsx"

export default function AdminPageBox ({children}) {

    const loggedInUserName = useSelector(state => state.LoginLogout.userId);
    const dispatch = useDispatch();
    const navigate = useNavigate()
    const handleLogout = () => {
        if(loggedInUserName){
          fetch('http://localhost:5000/admin/logout' , {
          method: 'POST',
          credentials:'include',
          }).then((response) => {
            if(response.ok){
            dispatch(ActionLoginLogout(''));
            alert(' .');
            navigate('/admin/login',{replace:true});
            
          }else{
            return response.json().then((data) => {
              throw new Error(data.error || '    .')
            })
          }
          }).catch((error) => {
            console.error(error.message);
            alert(error.message); 
          })
        } 
      }

    return (
        <>
        <div className="header">
            <Link to={"/admin/main"}>  </Link>
            {loggedInUserName} 
            <Link onClick={handleLogout}></Link>
        </div>
        <div>
            <aside className="tabMenu">
                <ul>
                    <li>
                      <NavLink to={'/admin/main'}></NavLink>
                    </li>
                    <li>
                      <NavLink to={'/admin/player'}></NavLink>
                    </li>
                    <li>
                      <NavLink to={'/admin/post'}></NavLink>
                    </li>
                </ul>
            </aside>
            {children}
        </div>
        </>
    )
}