import { useEffect , useState } from "react";
import { useParams, useNavigate, Link } from "react-router-dom"
import { formatDate } from "date-fns";
import Banner from "../Banner";

export default function Post (){

  const navigate = useNavigate();

  const { id } = useParams();
  const [post , setPost] = useState(null);
  const [ previousPost , setPreviousPost] = useState(null);
  const [ nextPost , setNextPost] = useState(null);

  useEffect(() => {
    const getPostData = async () => {
      try {
        const res = await fetch(`http://localhost:5000/post/${ id }`);
        if(!res.ok){
          throw new Error('   .')
        }
        const data = await res.json()
        
        setPost(data.post);
        setPreviousPost(data.previousPost);
        setNextPost(data.nextPost);
      } catch(error){
        alert(error.message)
        navigate(-1)
      }
    }
    getPostData();
  },[navigate,id])


  if(!post){
    return null;
  }

    return (<>
    <Banner aniWidth={'100%'} />
        <section className="size1442 post" >
            <h2>{post.title}</h2>
            <p className="postCreatedAtViews"><small></small><span>{formatDate(post.createdAt,'yyyy-MM-dd')}</span>|<small></small><span>{post.views}</span></p>
            <div>
                <img src="/images/fanZone/board_sample_img.png" alt="" />

                        ,  . 
                <br />
                <br />
                1.    <br />
                  .   1 / (/)<br />
                  <br />
                  <br />
                2.     <br />
                 .  :  <br /> 
                 .  :  ~ 2<br />
                   2     <br />
                  <br />
                  <br />
                3.  <br />
                  . 2024.9.12.() ~ 2024.9.23.()<br />
                  .  18:00  <br />
                  <br />
                  <br />
                4.  <br />
                  .   : recruit@incheonutd.com<br />
                  .    <br />
                  .         .<br />
                  <br />
                  <br />
                5. <br />
                  .   (032-880-5512)<br />

            </div>
            <ul>
              {previousPost &&
                <li>
                <Link to={`/fanZone/announcement/${previousPost.id}`}>
                    <span></span><div>{previousPost.title}</div><p className="postCreatedAtViews"><small></small><span>{formatDate(previousPost.createdAt,'yyyy-MM-dd')}</span>|<small></small><span>{previousPost.views}</span></p>
                  </Link>
                </li>
              }               
                <li>
                  <Link to={`/fanZone/announcement/${nextPost.id}`}>
                    <span></span><div>{nextPost.title}</div><p className="postCreatedAtViews"><small></small><span>{formatDate(nextPost.createdAt,'yyyy-MM-dd')}</span>|<small></small><span>{nextPost.views}</span></p>
                  </Link>
                </li>
            </ul>
        </section>
    </>
    )
}