import { useState } from "react";
import { useNavigate } from "react-router-dom"
import { useSelector } from "react-redux";
import Banner from "../Banner";

export default function WritePost (){
    
    const navigate = useNavigate();

    const loggedInUserName = useSelector(state => state.LoginLogout.userId);
    
    const [postValue , setPostValue] = useState({
        title: '',
        content : ''
    })

    if(!loggedInUserName){
        alert(' .');
        navigate('/login')
        return null;
    }

    const handleChange = (field,value) => {
        setPostValue({
            ...postValue,
            [field] : value,
        })
    }
    const handleSubmit =  async (e) => {
        e.preventDefault();
        if(postValue.title === '' || postValue.content === ''){
            return alert('    .')
        }
        try {
           const res = await fetch('http://localhost:5000/post/write' , {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify(postValue),
            credentials: 'include', //    
           })
           const data = await res.json();
           console.log(data);
           if(res.ok){
            alert(' .');
            navigate('/fanZone/cheeringGrounds');
           }else{
            alert(data.error)
           }
        }catch(error){
            alert('  .')
        }
    }



    return (<>
    <Banner aniWidth={'100%'} />
        <section className="size1442 postWrite" >
            <h2 className="hiddenH2"> </h2>
            <form onSubmit={(e)=> handleSubmit(e)} >
                <label htmlFor="postTitle"> </label>
                <input onChange={(e) => handleChange('title',e.target.value)} value={postValue.title} id="postTitle" type="text" placeholder=" " />
                <label htmlFor="postContent"> </label>
                <textarea onChange={(e) => handleChange('content', e.target.value)} value={postValue.content} id="postContent" placeholder="     "></textarea>
                <strong>  </strong>
                <ul>
                    <li>-           .</li>
                    <li>-   ,              .</li>
                    <li>-   HTML,         .</li>
                </ul>
                <button type="submit"> </button>
            </form>
        </section>
    </>
    )
}