import { useState } from "react";
import { Link , useNavigate } from "react-router-dom"
import data from "../../data.json"
import DaumPostcode from "react-daum-postcode";
import Modal from "react-modal";

const jobList = data.userJobSelectList;
const playerList = [];

data.pro["players"].forEach((el) => {
    el.profile.forEach((i) => {
        playerList.push(i.name);
    })
});


export default function MemberInformation () {
    const navigate = useNavigate();
    const [formData, setFormData] = useState({
      userId: '',
      userPw: '',
      userPwCheck: '',
      userPhoneNumber: '',
      address: '',
      favoritPlayer: '',
      selectedJob: '',
      singleOrMarried:'N',
      advertisement:'Y'
    });

    console.log(formData);

    
    //    1-ID , 2-PW , 3-PWCHECK , 4-PHONE , 5-ADRESS , 6-FAVORITE PLAYER
    const [isValid, setIsValid] = useState(Array(7).fill(''));
    console.log(isValid)
    const validators = {
        userId : (value) => /^[a-z0-9]{6,12}$/.test(value),
        userPw : (value) => /^(?=.*[A-Za-z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{9,12}$/.test(value),
        userPwCheck : (value) => value === formData.userPw,
        userPhoneNumber : (value) => value.length === 13 ? true : false,
        address : (value) => value !== '' ? true : false,
        favoritPlayer : (value) => value !== '' ? true : false,
    }
    
    const handleChange = (field, value) => {
        setFormData({
          ...formData,
          [field]: value,
        });
        if(field !== 'singleOrMarried' && field !== 'advertisement' && field !== 'selectedJob'){
        const validTemp = [...isValid];
        validTemp[Object.keys(validators).indexOf(field)] = validators[field](value);
        if(field === 'userId'){
            validTemp[6] = false;
            setIsValid(validTemp);
            return;
        }
        setIsValid(validTemp);
        }
      };

    
    //  
    //  '-'  
    const formatPhoneValue = (value) => {
        const transfromValue = value.replace(/[^0-9]/g, "");
        if(transfromValue.length <= 3){
            return transfromValue;
        }else if(transfromValue <= 7 ){
            return `${transfromValue.slice(0,3)}-${transfromValue.slice(3)}`
        }else {
             return `${transfromValue.slice(0,3)}-${transfromValue.slice(3,7)}-${transfromValue.slice(7,11)}`
        }
    }
    //       .
    function handleBlurPhone () {
        const formattedValue = formatPhoneValue(formData.userPhoneNumber);
        handleChange('userPhoneNumber',formattedValue);        
    }

    // 
    // react-modal  
    const [isModalOpen, setIsModalOpen] = useState(false); //  
    const openModal = () => {
       setIsModalOpen(true);
     };    
    
    //   ,  css => active class  
    const [isActive,setIsActive] = useState(Array(2).fill(null));
    function handleActive (index) {
        let temp = [...isActive];
        temp[index] = !temp[index];
        return setIsActive(temp);
    }

// userID  API     return (  API )
const handleDuplicateCheck = async () => {
    if(isValid[0] && formData.userId !== ''){
        try{
            const res = await fetch(
                `http://localhost:5000/user/duplicatecheck?userId=${encodeURIComponent(formData.userId)}`
              ); 
            if(!res.ok){
                throw new Error('    .');
            }
            const data = await res.json(); 
            if(data.isAvailable){
                alert(data.message);
                let vaildTemp = [...isValid];
                vaildTemp[6] = true;
                setIsValid(vaildTemp);
            }else{
                alert(data.message);
                // input value  
                handleChange('userId','');
            }
        }catch (error){
            alert(error);
        }
    }else{
        if(formData.userId === ''){
        return alert('  .')    
        }
        return alert('  .')
    }
}
    

    const handleSubmit = async (e) => {
        e.preventDefault(); //  form   
        if(isValid.every((el) => el === true)){
            try {
                const res = await fetch('http://localhost:5000/user/signUp', {
                    method: 'POST',
                    headers: {
                      'Content-Type': 'application/json',
                    },
                    body: JSON.stringify(formData),
                    credentials: 'include', //    
              });     
              const data = await res.json();
              if (res.ok) {
                alert(' !');
                navigate('/login/joinUs/Certification/MemberInformation/Registrationcomplete')
              } else {
                  alert(data.error || ' ');
                }
            } catch (error) {
                alert('   .');
            }
        }else{
            if(!isValid[1])alert(` 9~12  [],[],[]   .`)
            else if(!isValid[2])alert('  .')
            else if(!isValid[6])alert('  .')
            else alert('  !');
        }
      };


    return (
        <section className="memberInformationArea">
            <h2>  </h2>
            <p>    </p>
            <form onSubmit={handleSubmit} action="">
            <label htmlFor="userId"></label>
            <div className="input_box">
                <input onChange={(e) => handleChange('userId',e.target.value)}  className={isValid[0] ? "" : "active"} value={formData.userId}  id="userId" type="text" placeholder="6~ 12   ,  "/>
                <button onClick={handleDuplicateCheck} type="button" style={{cursor : isValid && formData.userId !== '' ? "pointer" : ""}} disabled={!isValid}></button>
                {!isValid[0] && formData.userId !== '' ? <strong>*  . 6~ 12   ,  </strong> : null}
            </div>
            <label htmlFor="userPassword"></label>
            <input  onChange={(e) => handleChange('userPw',e.target.value)} value={formData.userPw} id="userPassword" type="password" placeholder=" 9~12  [],[],[]   ."/>
            <small>  !@#$%^*+=-   .</small>
            <label htmlFor="userPasswordCheck"> </label>
            <input onChange={(e) => handleChange('userPwCheck',e.target.value)} value={formData.userPwCheck} id="userPasswordCheck" type="password" placeholder="  ." />
            <label htmlFor="userPhoneNumber"> </label>
            <input onBlur={() => handleBlurPhone()} onChange={(e) => handleChange('userPhoneNumber',e.target.value)} value={formData.userPhoneNumber} id="userPhoneNumber" type="text" />
            <span>" - "   .</span>
            <p> <mark>()</mark></p>
            <div className="input_box">
                <p>{formData.address === "" ? " ." : formData.address}</p>
                <button type="button"  onClick={openModal}> </button>
                <AdressModal handleChange={handleChange} isModalOpen={isModalOpen} setIsModalOpen={setIsModalOpen} formData={formData} setFormData={setFormData} />
            </div>
            <small>
                       .
            </small>
            <p>  <mark>()</mark></p>
            <div className="selectBox">
                <p onClick={() => handleActive(0)} className={isActive[0] ? "active" : ""}>
                    {formData.favoritPlayer === '' ? '  .' : formData.favoritPlayer}
                </p>
                    <ul className={isActive[0] ? "active scroll_layout" : "scroll_layout"}>
                        {playerList.map((el,index) => <li onClick={() => {handleChange('favoritPlayer',el)
                         handleActive(0)
                        }
                        } key={index}>{el}</li> )}
                    </ul>
            </div>
            <small>
                      ,  .
            </small>
            <p> <mark>()</mark></p>
            <div className="selectBox">
                <p onClick={() => handleActive(1)} className={isActive[1] ? "active" : ""}>
                    {formData.selectedJob === '' ? ' .' : formData.selectedJob} 
                </p>
                    <ul className={isActive[1] ? "active scroll_layout" : "scroll_layout"}>
                        {jobList.map((el,index) => <li key={index} onClick={() => {setFormData({...formData,selectedJob:el})
                            handleActive(1)
                        }}>{el}</li>)}
                    </ul>
            </div>
            <small>
                   ,     .<br />
                ()     "19    " <br />
                 .<br />
            </small>
            <p>  <mark>()</mark></p>
            <div className="check_box">
                <label>
                    <input onChange={(e) => handleChange('singleOrMarried',e.target.value)} type="radio" value="N" name="singleOrMarried" checked={formData.singleOrMarried === 'N'} />
                    
                </label>
                <label>
                    <input onChange={(e) => handleChange('singleOrMarried',e.target.value)} type="radio" value="Y" name="singleOrMarried" checked={formData.singleOrMarried === 'Y'} />
                    
                </label>
            </div>
            <small>
                     ,    .<br />
                (1)  -      .<br />
                (2)  -      .<br />
            </small>
            <p>   <mark>()</mark></p>
            <div className="check_box">
                <label>
                    <input onChange={(e) => handleChange('advertisement',e.target.value)} type="radio" value="Y" name="advertisement" checked={formData.advertisement === 'Y'} />
                    
                </label>
                <label>
                    <input onChange={(e) => handleChange('advertisement',e.target.value)} type="radio" value="N" name="advertisement" checked={formData.advertisement === 'N'} />
                    
                </label>
            </div>
            <small>
                   ,  ,   ,  ,<br />
                 .
            </small>
            <p className="scroll_layout">
            <br />
                        .<br />
            <br />
            1.     <br />
            -      , , ,      <br />
              <br />
            <br />
            2.   <br />
            - , , <br />
            <br />
            3.    <br />
            -       <br />
            <br />
            4.        <br />
                       ,<br />
                 .<br />
            <br />
                        .<br />
            <br />
            1.     <br />
            -      , , ,      <br />
              <br />
            <br />
            2.   <br />
            - , , <br />
            <br />
            3.    <br />
            -       <br />
            <br />
            4.        <br />
                       ,<br />
                 .<br />
            </p>
            <div className="submit_box">
                <Link></Link>
                <input type="submit" value=''/>
            </div>
            </form>
        </section>
    )
}

Modal.setAppElement("#root");

function AdressModal ({isModalOpen,setIsModalOpen,handleChange}) {
        const handleComplete = (data) => {
        const fullAddress = data.address; //  
        const extraAddress = data.bname || data.buildingName ? ` (${data.bname || data.buildingName})` : ""; //   
        handleChange('address',fullAddress + extraAddress);
        closeModal(); //  
      };
      //  
      const closeModal = () => {
        setIsModalOpen(false);
      };
    return (
        <Modal
        isOpen={isModalOpen}
        onRequestClose={closeModal}
        contentLabel=" "
        style={{
          overlay: { backgroundColor: "rgba(0, 0, 0, 0.80)", zIndex:"9999" , marginTop:"100px" , },
          content: { width: "400px", margin: "auto", paddingInline: "10px" , maxHeight : "500px"},
        }}
        >
            <DaumPostcode onComplete={handleComplete} />
        </Modal>
    )
}